@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,200&display=swap);
@font-face {
  font-family: "Linux Libertine";
  src: local("Linux Libertine"),
    url(/wp-content/plugins/react-plugins/react-app/build/static/media/LinLibertine_aDRS.2b16434b.ttf) format("truetype");
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.holder {
  width: 100%;
  height: 100%;
  background: rgb(0, 0, 0, 0.9);
  z-index: -1;
}

.search {
  color: #d9ab73;
  text-align: center;
  margin-bottom: 5vh;
}

@media (max-width: 991px) {
  .search {
    display: none;
  }
  .storeCards {
    margin-top: 20px;
  }
  .mobileSearch {
    display: block !important;
  }
  .desktopMap {
    display: none;
  }
  .mobileMap {
    display: block !important;
  }
}
.mobileMap {
  display: none;
}
.mobileSearch {
  color: #d9ab73;
  text-align: center;
  margin-bottom: 5vh;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1000;
  width: 100%;
  display: none;
}
.search h1 {
  margin-bottom: 1vh;
}

.mapCol {
  padding: 0px !important;
}

.selectedStoreInfo {
  padding: 0px !important;
}

.stickyMap {
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  padding: 0px !important;
}

#store {
  display: flex;
  width: auto;
  cursor: pointer;
  text-align: left;
  pointer-events: auto;
  flex-shrink: 0;
  max-width: 100%;
  align-items: center;
  color: #d9ab73;
  height: 22vh;
  position: relative;
  background: rgb(0, 0, 0, 0);
  font-family: "Linux Libertine";
}

#store img {
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  max-height: 100%;
  margin-top: -22px;
}

#store h4 {
  font-size: 1em;
  font-weight: bolder;
  margin-bottom: 6px;
  text-decoration: none;
  text-align: center;
  font-weight: 100;
  line-height: 0.9em;
}

#store svg.rotated {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  stroke-width: 0.2px;
  z-index: 1;
}

#store svg.line {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  stroke-width: 0.3px;
  z-index: 10;
}

#store p {
  text-align: center;
}

#storeInfo {
  margin: 0;
  position: absolute;
  top: 55%;
  transform: translateY(-50%);
  width: 90%;
  left: 2%;
}

.separatorBox {
  height: 10vw;
}

#store button {
  background: #d9ab73;
  border-radius: 10px;
  color: rgb(0, 0, 0.9);
  padding: 3px 2vw 3px 2vw;
  font-family: "Montserrat", sans-serif;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  font-style: italic;
  border: 1px solid transparent;
  font-size: 17px;
  font-weight: 800;
}

#buttonWrapper {
  background: transparent;
  padding: 0px;
  border: none;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  max-width: 600px;
}

#svgSeparator {
  width: 100%;
  height: 50px;
  margin-top: 1vh;
  margin-bottom: 1vh;
}

.lineBox {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 100%;
  stroke-width: 0.5;
  z-index: 10;
}

#logoColumn {
  padding-left: 3%;
}

#distanceToStore {
  color: white;
  font-size: 1.5em !important;
}

#storeContainer {
  background: transparent;
}

.search-bar-container {
  width: 90%;
  max-width: 600px;
  margin: 40px auto 0;
}

.search-input-container {
  position: relative;
}

.search-input#menuSearch {
  box-shadow: 0 2px 2px 0 rgb(0 0 0 / 16%), 0 0 0 1px rgb(0 0 0 / 8%);
  display: block;
  width: 100%;
  padding: 16px 16px 4px 36px;
  font-size: 16px;
  border-radius: 2px;
  outline: none;
  background: rgb(242, 230, 213, 0.53);
  color: white;
}

::-webkit-input-placeholder {
  color: rgb(242, 230, 213, 0.8);
  font-family: "Montserrat", sans-serif;
  border: 0;
  outline: 0;
  border-bottom: 1px solid rgb(242, 230, 213);
}

:-ms-input-placeholder {
  color: rgb(242, 230, 213, 0.8);
  font-family: "Montserrat", sans-serif;
  border: 0;
  outline: 0;
  border-bottom: 1px solid rgb(242, 230, 213);
}

::placeholder {
  color: rgb(242, 230, 213, 0.8);
  font-family: "Montserrat", sans-serif;
  border: 0;
  outline: 0;
  border-bottom: 1px solid rgb(242, 230, 213);
}

.icon {
  position: absolute;
  right: 20px;
  top: 13px;
  color: white;
}

.geoLocationButton {
  position: absolute;
  left: 10px;
  top: 13px;
  width: 20px;
  background: transparent;
  border: transparent;
  color: white;
  padding: 0px;
}

@media (max-width: 991px) {
  .search-input#menuSearch {
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 16%), 0 0 0 1px rgb(0 0 0 / 8%);
    display: block;
    width: 100%;
    padding: 16px 16px 4px 36px;
    font-size: 16px;
    border-radius: 2px;
    outline: none;
    background: rgb(23, 23, 23, 1);
    color: rgb(255, 255, 255);
  }
  ::-webkit-input-placeholder {
    color: rgb(242, 230, 213, 0.8);
    font-family: "Montserrat", sans-serif;
    border: 0;
    outline: 0;
    border-bottom: 1px solid rgb(242, 230, 213);
  }
  :-ms-input-placeholder {
    color: rgb(242, 230, 213, 0.8);
    font-family: "Montserrat", sans-serif;
    border: 0;
    outline: 0;
    border-bottom: 1px solid rgb(242, 230, 213);
  }
  ::placeholder {
    color: rgb(242, 230, 213, 0.8);
    font-family: "Montserrat", sans-serif;
    border: 0;
    outline: 0;
    border-bottom: 1px solid rgb(242, 230, 213);
  }

  .icon {
    color: rgb(255, 255, 255, 0.7);
  }

  .geoLocationButton {
    color: rgb(255, 255, 255, 0.7);
  }
}

.storeImage {
  width: 100%;
}
.storeLogo {
  width: 80%;
  padding-top: 10px;
  margin-bottom: 15px;
}
.infoWindow {
  max-width: 250px;
  text-align: center;
  font-family: "linux liberte";
  background-color: rgb(0, 0, 0, 0.9);
  color: #f2e6d5;
}

#address {
  font-size: 1.4em;
  line-height: 1.5;
  margin-bottom: 0px;
}

.phone {
  padding-bottom: 15px;
  margin: 0px;
}

.gm-style-iw {
  width: 250px !important;
  top: 0 !important;
  left: 0 !important;
  background-color: rgb(0, 0, 0, 0.9);
  box-shadow: 0 1px 6px rgba(178, 178, 178, 0.6);
  padding: 0px !important;
  overflow: hidden !important;
}

.gm-style-iw-d {
  overflow: auto !important;
}

.containerPadding {
  padding: 0px;
  background-color: transparent !important;
}

.stickyContainer {
  position: -webkit-sticky;
  position: sticky;
  top: 45px !important;
  padding-top: 20px;
  background-color: rgb(23, 23, 23) !important;
  z-index: 100;
}

.logo {
  width: 50%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  height: 100% !important;
  max-height: 100%;
  margin-top: 15px;
  margin-bottom: 15px;
}

.backDiv {
  position: relative;
}

.backButton {
  position: absolute;
  width: 20px;
  top: 40%;
  left: 5vw !important;
  background: transparent;
  border: transparent;
  color: #d9ab73;
  padding: 0px !important;
}

@media (max-width: 768px) {
  .backButton {
    left: 5vh;
  }
}

.buttonContainer {
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  margin-bottom: 15px;
}

.button {
  background: #d9ab73;
  border-radius: 10px;
  color: rgb(0, 0, 0.9);
  font-family: "Montserrat", sans-serif;
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  font-weight: bolder;
  padding: 0px !important;
}

.tri {
  padding: 20px 1vw 20px 1vw !important;
  min-width: 14vh;
  margin-right: 3px;
  margin-left: 3px;
}

.duo {
  padding: 20px 2vw 20px 2vw !important;
  min-width: 18vh;
  margin-right: 3px;
