}

.phoneAndAddress {
  color: #f2e6d5;
  margin: auto;
  text-align: Center;
  margin-top: 15px;
  width: 75%;
  margin-bottom: 15px;
  font-family: "Linux Libertine";
}

.storeImage {
  width: 100%;
}

.phoneAndAddress {
  color: #f2e6d5;
  margin: auto;
  text-align: Center;
  margin-top: 15px;
  width: 85%;
  margin-bottom: 15px;
}

.innerPhoneAndAddress {
  border-top: 1px solid rgb(242, 230, 213, 0.3);
  border-bottom: 1px solid rgb(242, 230, 213, 0.3);
}

.phoneIcon {
  color: #d9ab73;
  margin-bottom: 10px;
  margin-top: 15px;
}

.locationIcon {
  color: #d9ab73;
  margin-top: 10px;
  margin-bottom: 10px;
}

.phoneNumber {
  margin-bottom: 15px;
}

.Hours {
  color: #f2e6d5;
  text-align: center;
  width: 85%;
  margin: auto;
  font-size: 1.5rem;
  margin-bottom: 25px;
}
.Hours h4 {
  font-size: 2.5rem;
  font-family: "Linux Libertine";
}

.storeDescription {
  width: 85%;
  color: #f2e6d5;
  text-align: center;
  margin: auto;
  margin-top: 25px;
  margin-bottom: 20px;
}
.storeDescription h4 {
  border-top: 1px solid rgb(242, 230, 213, 0.3);
  border-bottom: 1px solid rgb(242, 230, 213, 0.3);
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 2.5rem;
  font-family: "Linux Libertine";
}
.storeDescription p {
  width: 85%;
  margin: auto;
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  text-align: justify;
  font-size: 17px;
  font-weight: 400;
}
.phoneLink {
  color: inherit;
}
.phoneLink:hover {
  color: #d9ab73;
}
.hoverAddress {
  cursor: pointer;
}
.hoverAddress:hover {
  color: #d9ab73;
}

.hourRow {
  box-shadow: rgba(0, 0, 0, 0.75) 0px 0px 6px;
  margin-bottom: 15px;
  border-top: 1px solid rgb(242, 230, 213, 0.3);
  border-bottom: 1px solid rgb(242, 230, 213, 0.3);
  font-family: "Linux Libertine";
}

.day {
  text-transform: capitalize;
  text-align: left;
  font-family: "Linux Libertine";
}
.hours {
  text-align: right;
  font-family: "Linux Libertine";
}
@media only screen and (max-width: 400px) {
  .hours {
    font-size: 0.8em;
  }
}
.hourContainer {
  background-color: transparent !important;
}

